import { mapGetters } from 'vuex';

export default {
  name: "MessageItem",
  props: {
    message: {
      Type: Object,
      required: true
    }
  },
  computed: {
    ...mapGetters('user', ['user']),
    bgColor() {
      return this.user.id === this.message.senderId ? 'amber-7' : 'blue-7';
    },
    fadeStyle() {
      return {
        fadeInLeft: this.user.id === this.message.senderId,
        fadeInRight: this.user.id !== this.message.senderId
      }
    }
  }
}
