export default {
  name: "UserPane",
  props: {
    user: {
      Type: Object,
      required: true
    }
  },
  methods: {
    handleMicClick() {
      this.$emit('handleChangeMicState')
    }
  },
  computed: {
    micIsActive() {
      return {
        "micActive": this.user.speaking,
        "micOff": !this.user.speaking
      }
    }
  }
}
